'use client';

import { useSession } from 'next-auth/react';

export default function Home() {
  const { data: session, status } = useSession();

  if (status === 'loading') return <p>加载中...</p>;

  if (!session) return <p>未登录，请先 <a href="/login">登录</a></p>;

  const role = session.user.role;

  if (role === 'admin') {
    return <h1>欢迎管理员：{session.user.name}</h1>;
  } else if (role === 'manager') {
    return <h1>欢迎项目经理：{session.user.name}</h1>;
  } else if (role === 'member') {
    return <h1>欢迎接单员：{session.user.name}</h1>;
  } else {
    return <h1>未知用户角色</h1>;
  }
}
